"use client"

import { useState, useRef, useEffect } from 'react'
import { SLOT_SYMBOLS, GAME_CONFIG } from '@/lib/constants'
import { useGameSounds } from '@/hooks/useGameSounds'

interface GameState {
  fichasAdministrativas: number
  setFichasAdministrativas: (value: number) => void
  creditosVIP: number
  setCreditosVIP: (value: number) => void
  puntosExito: number
  setPuntosExito: (value: number) => void
  nivelActual: number
  setNivelActual: (value: number) => void
  modoLigero: boolean
  setModoLigero: (value: boolean) => void
}

interface TaskSlotsProps {
  gameState: GameState
}

export default function TaskSlots({ gameState }: TaskSlotsProps) {
  const [isSpinning, setIsSpinning] = useState(false)
  const [currentBet, setCurrentBet] = useState(GAME_CONFIG.minBet)
  const [reels, setReels] = useState([0, 0, 0])
  const [lastWin, setLastWin] = useState<number | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [showConfetti, setShowConfetti] = useState(false)

  const { fichasAdministrativas, setFichasAdministrativas, setPuntosExito, modoLigero } = gameState
  const { playSlotSpinSound, playSlotWinSound, playErrorSound, playClickSound, playJackpotSound } = useGameSounds()

  const spinSlots = async () => {
    if (isSpinning || fichasAdministrativas < currentBet) {
      setError('Fichas insuficientes o mÃ¡quina en funcionamiento')
      playErrorSound()
      return
    }

    setError(null)
    setIsSpinning(true)
    setLastWin(null)
    setShowConfetti(false)

    // Deducir apuesta
    setFichasAdministrativas(fichasAdministrativas - currentBet)

    // Sonido de giro
    playSlotSpinSound()

    // Simular giro de carretes con diferentes duraciones
    const spinDurations = [1000, 1500, 2000]
    const finalReels = [
      Math.floor(Math.random() * SLOT_SYMBOLS.length),
      Math.floor(Math.random() * SLOT_SYMBOLS.length),
      Math.floor(Math.random() * SLOT_SYMBOLS.length)
    ]

    // Animar cada carrete
    spinDurations.forEach((duration, index) => {
      const startTime = Date.now()
      const animate = () => {
        const elapsed = Date.now() - startTime
        const progress = Math.min(elapsed / duration, 1)

        if (progress < 1) {
          // Cambiar sÃ­mbolos rÃ¡pidamente durante el giro
          setReels(prev => {
            const newReels = [...prev]
            newReels[index] = Math.floor(Math.random() * SLOT_SYMBOLS.length)
            return newReels
          })
          requestAnimationFrame(animate)
        } else {
          // Establecer sÃ­mbolo final
          setReels(prev => {
            const newReels = [...prev]
            newReels[index] = finalReels[index]
            return newReels
          })

          // Si es el Ãºltimo carrete, calcular resultado
          if (index === 2) {
            setTimeout(() => {
              calculateWin(finalReels)
              setIsSpinning(false)
            }, 200)
          }
        }
      }
      animate()
    })
  }

  const calculateWin = (finalReels: number[]) => {
    const symbols = finalReels.map(index => SLOT_SYMBOLS[index])
    
    // Verificar combinaciones ganadoras
    let winAmount = 0
    let winType = ''

    // Tres iguales (Jackpot)
    if (symbols[0].id === symbols[1].id && symbols[1].id === symbols[2].id) {
      winAmount = currentBet * symbols[0].value * 3
      winType = 'JACKPOT'
      setShowConfetti(true)
      playJackpotSound()
    }
    // Dos iguales
    else if (symbols[0].id === symbols[1].id || symbols[1].id === symbols[2].id || symbols[0].id === symbols[2].id) {
      const matchingSymbol = symbols[0].id === symbols[1].id ? symbols[0] : 
                           symbols[1].id === symbols[2].id ? symbols[1] : symbols[0]
      winAmount = currentBet * matchingSymbol.value
      winType = 'DOBLE'
      playSlotWinSound()
    }

    if (winAmount > 0) {
      setLastWin(winAmount)
      setFichasAdministrativas(fichasAdministrativas + winAmount)
      setPuntosExito(gameState.puntosExito + Math.floor(winAmount / 5))
    }
  }

  // Efecto de confetti
  useEffect(() => {
    if (showConfetti && !modoLigero) {
      const timer = setTimeout(() => setShowConfetti(false), 3000)
      return () => clearTimeout(timer)
    }
  }, [showConfetti, modoLigero])

  return (
    <div className="p-6 space-y-6 relative">
      {/* Confetti Effect */}
      {showConfetti && !modoLigero && (
        <div className="absolute inset-0 pointer-events-none z-10">
          <div className="absolute top-1/4 left-1/4 w-2 h-2 bg-neonYellow animate-bounce"></div>
          <div className="absolute top-1/3 right-1/4 w-2 h-2 bg-neonFuchsia animate-bounce" style={{ animationDelay: '0.1s' }}></div>
          <div className="absolute top-1/2 left-1/3 w-2 h-2 bg-neonTurquoise animate-bounce" style={{ animationDelay: '0.2s' }}></div>
          <div className="absolute top-2/3 right-1/3 w-2 h-2 bg-neonGreen animate-bounce" style={{ animationDelay: '0.3s' }}></div>
        </div>
      )}

      <div className="text-center">
        <h2 className="text-3xl font-casino mb-2 bg-gradient-to-r from-neonTurquoise to-neonYellow bg-clip-text text-transparent">
          Tragamonedas de Tareas
        </h2>
        <p className="text-gray-400">
          Alinea los sÃ­mbolos administrativos para ganar
        </p>
      </div>

      {/* MÃ¡quina Tragamonedas */}
      <div className="max-w-lg mx-auto">
        <div className="bg-gradient-to-b from-gray-800 to-gray-900 rounded-xl p-6 border-2 border-neonTurquoise/30 shadow-lg shadow-neonTurquoise/20">
          {/* Pantalla de Carretes */}
          <div className="bg-black rounded-lg p-4 mb-4 border border-gray-600">
            <div className="grid grid-cols-3 gap-4">
              {reels.map((reelIndex, index) => (
                <div
                  key={index}
                  className={`bg-gray-800 rounded-lg p-6 text-center border-2 transition-all duration-300 ${
                    isSpinning 
                      ? 'border-neonYellow animate-pulse' 
                      : 'border-gray-600'
                  }`}
                >
                  <div className={`text-4xl mb-2 ${isSpinning ? 'animate-spin-slow' : ''}`}>
                    {SLOT_SYMBOLS[reelIndex].symbol}
                  </div>
                  <div className="text-xs text-gray-400">
                    {SLOT_SYMBOLS[reelIndex].name}
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Controles de Apuesta */}
          <div className="space-y-4">
            <div className="text-center">
              <label className="block text-sm text-gray-400 mb-2">
                Apuesta (Fichas Administrativas)
              </label>
              <div className="flex items-center justify-center space-x-4">
                <button
                  onClick={() => {
                    playClickSound()
                    setCurrentBet(Math.max(GAME_CONFIG.minBet, currentBet - 10))
                  }}
                  disabled={isSpinning}
                  className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded disabled:opacity-50 transition-all duration-200 hover:scale-105"
                >
                  -10
                </button>
                <span className="font-casino text-xl text-neonYellow min-w-[80px] animate-glow">
                  {currentBet}
                </span>
                <button
                  onClick={() => {
                    playClickSound()
                    setCurrentBet(Math.min(GAME_CONFIG.maxBet, currentBet + 10))
                  }}
                  disabled={isSpinning}
                  className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded disabled:opacity-50 transition-all duration-200 hover:scale-105"
                >
                  +10
                </button>
              </div>
            </div>

            <button
              onClick={spinSlots}
              disabled={isSpinning || fichasAdministrativas < currentBet}
              className={`w-full py-3 px-6 rounded-lg font-casino text-lg transition-all duration-300 ${
                isSpinning || fichasAdministrativas < currentBet
                  ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
                  : 'bg-gradient-to-r from-neonTurquoise to-neonYellow hover:shadow-lg hover:shadow-neonTurquoise/30 text-black font-bold'
              }`}
            >
              {isSpinning ? 'GIRANDO...' : 'GIRAR CARRETES'}
            </button>
          </div>
        </div>
      </div>

      {/* Resultados y Errores */}
      {error && (
        <div className="text-center p-3 bg-red-900/30 border border-red-500/50 rounded-lg">
          <p className="text-red-400">{error}</p>
        </div>
      )}

      {lastWin && (
        <div className="text-center p-4 bg-green-900/30 border border-neonGreen/50 rounded-lg">
          <p className="text-neonGreen font-casino text-xl">
            Â¡GANASTE {lastWin} FICHAS!
          </p>
          {showConfetti && (
            <p className="text-neonYellow text-sm mt-1">
              Â¡JACKPOT ADMINISTRATIVO!
            </p>
          )}
        </div>
      )}

      {/* Tabla de Pagos */}
      <div className="bg-gray-800/50 rounded-lg p-4">
        <h3 className="font-casino text-lg text-neonTurquoise mb-3 text-center">
          Tabla de Pagos
        </h3>
        <div className="space-y-2 text-sm">
          <div className="flex justify-between items-center p-2 bg-gray-700/50 rounded">
            <span>3 SÃ­mbolos Iguales</span>
            <span className="text-neonYellow font-casino">Apuesta Ã— Valor Ã— 3</span>
          </div>
          <div className="flex justify-between items-center p-2 bg-gray-700/50 rounded">
            <span>2 SÃ­mbolos Iguales</span>
            <span className="text-neonYellow font-casino">Apuesta Ã— Valor</span>
          </div>
          <div className="grid grid-cols-2 gap-2 mt-4">
            {SLOT_SYMBOLS.map((symbol) => (
              <div key={symbol.id} className="flex items-center justify-between p-2 bg-gray-700/30 rounded">
                <div className="flex items-center space-x-2">
                  <span className="text-lg">{symbol.symbol}</span>
                  <span className="text-xs">{symbol.name}</span>
                </div>
                <span className="text-neonYellow font-casino text-sm">
                  {symbol.value}x
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
