"use client"

import { useState, useEffect } from 'react'
import { RAFFLE_PRIZES, GAME_CONFIG } from '@/lib/constants'

interface GameState {
  fichasAdministrativas: number
  setFichasAdministrativas: (value: number) => void
  creditosVIP: number
  setCreditosVIP: (value: number) => void
  puntosExito: number
  setPuntosExito: (value: number) => void
  nivelActual: number
  setNivelActual: (value: number) => void
  modoLigero: boolean
  setModoLigero: (value: boolean) => void
}

interface ResourceRaffleProps {
  gameState: GameState
}

interface RaffleHistory {
  prize: string
  rarity: string
  value: number
  timestamp: Date
}

export default function ResourceRaffle({ gameState }: ResourceRaffleProps) {
  const [isSpinning, setIsSpinning] = useState(false)
  const [lastPrize, setLastPrize] = useState<typeof RAFFLE_PRIZES[0] | null>(null)
  const [timeUntilNext, setTimeUntilNext] = useState<number>(0)
  const [canSpin, setCanSpin] = useState(true)
  const [raffleHistory, setRaffleHistory] = useState<RaffleHistory[]>([])
  const [currentRotation, setCurrentRotation] = useState(0)

  const { creditosVIP, setCreditosVIP, setPuntosExito, modoLigero } = gameState

  // Simular tiempo hasta prÃ³ximo bono (para demo, usamos 30 segundos en lugar de 7 dÃ­as)
  useEffect(() => {
    const lastSpin = localStorage.getItem('lastRaffleSpin')
    const now = Date.now()
    
    if (lastSpin) {
      const timeSinceLastSpin = now - parseInt(lastSpin)
      const cooldownTime = 30000 // 30 segundos para demo (en producciÃ³n serÃ­a 7 dÃ­as)
      
      if (timeSinceLastSpin < cooldownTime) {
        setCanSpin(false)
        setTimeUntilNext(cooldownTime - timeSinceLastSpin)
      }
    }

    // Cargar historial del localStorage
    const savedHistory = localStorage.getItem('raffleHistory')
    if (savedHistory) {
      setRaffleHistory(JSON.parse(savedHistory))
    }
  }, [])

  // Countdown timer
  useEffect(() => {
    if (timeUntilNext > 0) {
      const timer = setInterval(() => {
        setTimeUntilNext(prev => {
          if (prev <= 1000) {
            setCanSpin(true)
            return 0
          }
          return prev - 1000
        })
      }, 1000)

      return () => clearInterval(timer)
    }
  }, [timeUntilNext])

  const spinRaffle = async () => {
    if (isSpinning || !canSpin) return

    setIsSpinning(true)
    setLastPrize(null)

    // Costo en crÃ©ditos VIP
    const spinCost = 25
    if (creditosVIP >= spinCost) {
      setCreditosVIP(creditosVIP - spinCost)
    }

    // AnimaciÃ³n de giro
    const spins = 3 + Math.random() * 4 // 3-7 vueltas
    const finalRotation = currentRotation + spins * 360 + Math.random() * 360
    
    const duration = modoLigero ? 2000 : 4000
    const startTime = Date.now()
    const startRotation = currentRotation

    const animate = () => {
      const elapsed = Date.now() - startTime
      const progress = Math.min(elapsed / duration, 1)
      
      // Easing function
      const easeOut = 1 - Math.pow(1 - progress, 3)
      const rotation = startRotation + (finalRotation - startRotation) * easeOut
      
      setCurrentRotation(rotation)

      if (progress < 1) {
        requestAnimationFrame(animate)
      } else {
        // Determinar premio basado en rareza
        const rand = Math.random()
        let selectedPrize
        
        if (rand < 0.5) {
          selectedPrize = RAFFLE_PRIZES.find(p => p.rarity === 'comÃºn')
        } else if (rand < 0.8) {
          selectedPrize = RAFFLE_PRIZES.find(p => p.rarity === 'raro')
        } else if (rand < 0.95) {
          selectedPrize = RAFFLE_PRIZES.find(p => p.rarity === 'Ã©pico')
        } else {
          selectedPrize = RAFFLE_PRIZES.find(p => p.rarity === 'legendario')
        }

        if (selectedPrize) {
          setLastPrize(selectedPrize)
          
          // Aplicar premio
          if (selectedPrize.name.includes('VIP')) {
            setCreditosVIP(creditosVIP + selectedPrize.value)
          } else {
            setPuntosExito(gameState.puntosExito + selectedPrize.value)
          }

          // Guardar en historial
          const newEntry: RaffleHistory = {
            prize: selectedPrize.name,
            rarity: selectedPrize.rarity,
            value: selectedPrize.value,
            timestamp: new Date()
          }
          
          const updatedHistory = [newEntry, ...raffleHistory].slice(0, 10)
          setRaffleHistory(updatedHistory)
          localStorage.setItem('raffleHistory', JSON.stringify(updatedHistory))
        }

        // Establecer cooldown
        const now = Date.now()
        localStorage.setItem('lastRaffleSpin', now.toString())
        setCanSpin(false)
        setTimeUntilNext(30000) // 30 segundos para demo
        setIsSpinning(false)
      }
    }

    animate()
  }

  const formatTime = (ms: number) => {
    const seconds = Math.floor(ms / 1000)
    const minutes = Math.floor(seconds / 60)
    const hours = Math.floor(minutes / 60)
    
    if (hours > 0) {
      return `${hours}h ${minutes % 60}m ${seconds % 60}s`
    } else if (minutes > 0) {
      return `${minutes}m ${seconds % 60}s`
    } else {
      return `${seconds}s`
    }
  }

  const getRarityColor = (rarity: string) => {
    switch (rarity) {
      case 'comÃºn': return 'text-gray-400'
      case 'raro': return 'text-blue-400'
      case 'Ã©pico': return 'text-purple-400'
      case 'legendario': return 'text-yellow-400'
      default: return 'text-white'
    }
  }

  const getRarityBorder = (rarity: string) => {
    switch (rarity) {
      case 'comÃºn': return 'border-gray-400'
      case 'raro': return 'border-blue-400'
      case 'Ã©pico': return 'border-purple-400'
      case 'legendario': return 'border-yellow-400'
      default: return 'border-white'
    }
  }

  return (
    <div className="p-6 space-y-6">
      <div className="text-center">
        <h2 className="text-3xl font-casino mb-2 bg-gradient-to-r from-neonGreen to-neonOrange bg-clip-text text-transparent">
          TÃ³mbola de Recursos
        </h2>
        <p className="text-gray-400">
          Bono semanal especial con premios exclusivos
        </p>
      </div>

      {/* Rueda de la TÃ³mbola */}
      <div className="max-w-lg mx-auto">
        <div className="relative">
          {/* Rueda */}
          <div className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-full p-8 border-4 border-neonGreen/30 shadow-lg shadow-neonGreen/20">
            <div 
              className="w-64 h-64 mx-auto rounded-full border-4 border-neonOrange relative transition-transform duration-1000 ease-out"
              style={{ 
                transform: `rotate(${currentRotation}deg)`,
                background: `conic-gradient(
                  from 0deg,
                  #FF00FF 0deg 90deg,
                  #00FFF0 90deg 180deg,
                  #FFFF00 180deg 270deg,
                  #00FF00 270deg 360deg
                )`
              }}
            >
              {/* Sectores de premios */}
              {RAFFLE_PRIZES.map((prize, index) => (
                <div
                  key={prize.id}
                  className="absolute text-black font-bold text-xs text-center"
                  style={{
                    top: '50%',
                    left: '50%',
                    transform: `rotate(${index * 90}deg) translateY(-80px) rotate(-${index * 90}deg)`,
                    transformOrigin: '0 80px',
                    width: '60px',
                    marginLeft: '-30px'
                  }}
                >
                  {prize.name.split(' ')[0]}
                </div>
              ))}
              
              {/* Centro de la rueda */}
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-16 h-16 bg-black rounded-full border-2 border-white flex items-center justify-center">
                <div className="text-neonGreen font-casino text-xs">SPIN</div>
              </div>
            </div>
          </div>

          {/* Indicador */}
          <div className="absolute top-4 left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-b-8 border-l-transparent border-r-transparent border-b-white z-10"></div>
        </div>

        {/* Controles */}
        <div className="text-center mt-6 space-y-4">
          {canSpin ? (
            <button
              onClick={spinRaffle}
              disabled={isSpinning || creditosVIP < 25}
              className={`px-8 py-4 rounded-lg font-casino text-lg transition-all duration-300 ${
                isSpinning || creditosVIP < 25
                  ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
                  : 'bg-gradient-to-r from-neonGreen to-neonOrange text-black hover:shadow-lg hover:shadow-neonGreen/30'
              }`}
            >
              {isSpinning ? 'GIRANDO...' : 'GIRAR TÃ“MBOLA (25 VIP)'}
            </button>
          ) : (
            <div className="p-4 bg-gray-800/50 rounded-lg border border-gray-600">
              <p className="text-neonOrange font-casino text-lg">
                PrÃ³ximo giro disponible en:
              </p>
              <p className="text-2xl font-casino text-neonYellow">
                {formatTime(timeUntilNext)}
              </p>
            </div>
          )}

          {creditosVIP < 25 && canSpin && (
            <p className="text-red-400 text-sm">
              Necesitas al menos 25 CrÃ©ditos VIP para girar
            </p>
          )}
        </div>
      </div>

      {/* Ãšltimo Premio */}
      {lastPrize && (
        <div className={`text-center p-4 rounded-lg border-2 ${getRarityBorder(lastPrize.rarity)} bg-gray-800/50`}>
          <p className="text-neonGreen font-casino text-lg mb-2">
            Â¡PREMIO OBTENIDO!
          </p>
          <p className={`font-casino text-xl ${getRarityColor(lastPrize.rarity)}`}>
            {lastPrize.name}
          </p>
          <p className="text-neonYellow text-sm">
            +{lastPrize.value} {lastPrize.name.includes('VIP') ? 'CrÃ©ditos VIP' : 'Puntos de Ã‰xito'}
          </p>
        </div>
      )}

      {/* Historial de Premios */}
      <div className="bg-gray-800/50 rounded-lg p-4">
        <h3 className="font-casino text-lg text-neonGreen mb-3 text-center">
          Historial de Premios
        </h3>
        {raffleHistory.length > 0 ? (
          <div className="space-y-2 max-h-48 overflow-y-auto">
            {raffleHistory.map((entry, index) => (
              <div key={index} className="flex justify-between items-center p-2 bg-gray-700/50 rounded">
                <div>
                  <span className={`font-casino ${getRarityColor(entry.rarity)}`}>
                    {entry.prize}
                  </span>
                  <div className="text-xs text-gray-400">
                    {entry.timestamp.toLocaleString()}
                  </div>
                </div>
                <span className="text-neonYellow font-casino">
                  +{entry.value}
                </span>
              </div>
            ))}
          </div>
        ) : (
          <p className="text-gray-400 text-center py-4">
            No hay premios en el historial
          </p>
        )}
      </div>

      {/* Tabla de Premios */}
      <div className="bg-gray-800/50 rounded-lg p-4">
        <h3 className="font-casino text-lg text-neonOrange mb-3 text-center">
          Premios Disponibles
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
          {RAFFLE_PRIZES.map((prize) => (
            <div key={prize.id} className={`p-3 rounded-lg border ${getRarityBorder(prize.rarity)} bg-gray-700/30`}>
              <div className="flex justify-between items-center">
                <div>
                  <div className={`font-casino ${getRarityColor(prize.rarity)}`}>
                    {prize.name}
                  </div>
                  <div className="text-xs text-gray-400 capitalize">
                    {prize.rarity}
                  </div>
                </div>
                <div className="text-neonYellow font-casino">
                  +{prize.value}
                </div>
              </div>
            </div>
          ))}
        </div>
        
        <div className="mt-4 pt-4 border-t border-gray-600 text-sm text-gray-400">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-2 text-center">
            <div>
              <div className="text-gray-400">ComÃºn</div>
              <div className="text-sm">50%</div>
            </div>
            <div>
              <div className="text-blue-400">Raro</div>
              <div className="text-sm">30%</div>
            </div>
            <div>
              <div className="text-purple-400">Ã‰pico</div>
              <div className="text-sm">15%</div>
            </div>
            <div>
              <div className="text-yellow-400">Legendario</div>
              <div className="text-sm">5%</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
