"use client"

import { useState } from 'react'
import { PROGRESSION_LEVELS } from '@/lib/constants'

interface GameState {
  fichasAdministrativas: number
  setFichasAdministrativas: (value: number) => void
  creditosVIP: number
  setCreditosVIP: (value: number) => void
  puntosExito: number
  setPuntosExito: (value: number) => void
  nivelActual: number
  setNivelActual: (value: number) => void
  modoLigero: boolean
  setModoLigero: (value: boolean) => void
}

interface HeaderProps {
  gameState: GameState
}

export default function Header({ gameState }: HeaderProps) {
  const { modoLigero, setModoLigero, nivelActual, puntosExito } = gameState
  const [showStats, setShowStats] = useState(false)

  const currentLevel = PROGRESSION_LEVELS.find(level => level.level === nivelActual)
  const nextLevel = PROGRESSION_LEVELS.find(level => level.level === nivelActual + 1)
  
  const progressPercentage = nextLevel 
    ? ((puntosExito - (currentLevel?.requiredPoints || 0)) / (nextLevel.requiredPoints - (currentLevel?.requiredPoints || 0))) * 100
    : 100

  return (
    <header className="bg-darkCard border-b border-darkBorder shadow-lg">
      <div className="container mx-auto px-4 py-4">
        <div className="flex flex-col md:flex-row justify-between items-center gap-4">
          {/* Logo y TÃ­tulo */}
          <div className="flex items-center space-x-4">
            <h1 className="text-3xl md:text-4xl font-casino font-bold bg-gradient-to-r from-neonFuchsia via-neonTurquoise to-neonYellow bg-clip-text text-transparent animate-glow">
              PahYmiFunciÃ³n
            </h1>
            <div className="hidden md:block text-sm text-gray-400">
              Casino Administrativo
            </div>
          </div>

          {/* Barra de Progreso */}
          <div className="flex-1 max-w-md mx-4">
            <div className="text-center mb-2">
              <span className="text-sm font-casino text-neonGreen">
                {currentLevel?.title || 'Becario'}
              </span>
              {nextLevel && (
                <span className="text-xs text-gray-400 ml-2">
                  â†’ {nextLevel.title}
                </span>
              )}
            </div>
            <div className="w-full bg-gray-700 rounded-full h-2">
              <div 
                className="bg-gradient-to-r from-neonGreen to-neonYellow h-2 rounded-full transition-all duration-500"
                style={{ width: `${Math.min(progressPercentage, 100)}%` }}
              />
            </div>
            <div className="text-xs text-gray-400 text-center mt-1">
              {nextLevel ? `${puntosExito}/${nextLevel.requiredPoints} puntos` : 'Nivel MÃ¡ximo'}
            </div>
          </div>

          {/* Controles */}
          <div className="flex items-center space-x-4">
            <button
              onClick={() => setShowStats(!showStats)}
              className="px-3 py-2 text-sm border border-neonTurquoise/50 rounded-lg hover:border-neonTurquoise hover:bg-neonTurquoise/10 transition-all duration-300"
            >
              Stats
            </button>
            
            <button
              onClick={() => setModoLigero(!modoLigero)}
              className={`px-4 py-2 text-sm rounded-lg border transition-all duration-300 ${
                modoLigero 
                  ? 'border-neonYellow bg-neonYellow/20 text-neonYellow' 
                  : 'border-neonFuchsia/50 hover:border-neonFuchsia hover:bg-neonFuchsia/10'
              }`}
            >
              {modoLigero ? 'Modo Completo' : 'Modo Ligero'}
            </button>
          </div>
        </div>

        {/* Panel de EstadÃ­sticas Expandible */}
        {showStats && (
          <div className="mt-4 p-4 bg-gray-800/50 rounded-lg border border-gray-600">
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
              <div>
                <div className="text-gray-400">Nivel Actual</div>
                <div className="text-neonGreen font-casino">{currentLevel?.title}</div>
              </div>
              <div>
                <div className="text-gray-400">Tema Activo</div>
                <div className="text-neonTurquoise capitalize">{currentLevel?.theme}</div>
              </div>
              <div>
                <div className="text-gray-400">Progreso</div>
                <div className="text-neonYellow">{Math.round(progressPercentage)}%</div>
              </div>
              <div>
                <div className="text-gray-400">Modo</div>
                <div className="text-neonFuchsia">{modoLigero ? 'Ligero' : 'Completo'}</div>
              </div>
            </div>
          </div>
        )}
      </div>
    </header>
  )
}
