"use client"

import { useCallback, useRef } from 'react'

export const useGameSounds = () => {
  const audioContextRef = useRef<AudioContext | null>(null)

  // Inicializar AudioContext
  const initAudioContext = useCallback(() => {
    if (!audioContextRef.current) {
      audioContextRef.current = new (window.AudioContext || (window as any).webkitAudioContext)()
    }
    return audioContextRef.current
  }, [])

  // Crear sonido sintÃ©tico
  const createTone = useCallback((frequency: number, duration: number, type: OscillatorType = 'sine') => {
    const audioContext = initAudioContext()
    const oscillator = audioContext.createOscillator()
    const gainNode = audioContext.createGain()

    oscillator.connect(gainNode)
    gainNode.connect(audioContext.destination)

    oscillator.frequency.setValueAtTime(frequency, audioContext.currentTime)
    oscillator.type = type

    gainNode.gain.setValueAtTime(0.3, audioContext.currentTime)
    gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + duration)

    oscillator.start(audioContext.currentTime)
    oscillator.stop(audioContext.currentTime + duration)
  }, [initAudioContext])

  // Sonidos especÃ­ficos del juego
  const playRouletteTickSound = useCallback(() => {
    createTone(800, 0.1, 'square')
  }, [createTone])

  const playRouletteWinSound = useCallback(() => {
    // Secuencia de tonos ascendentes
    setTimeout(() => createTone(523, 0.2), 0)    // C5
    setTimeout(() => createTone(659, 0.2), 100)  // E5
    setTimeout(() => createTone(784, 0.3), 200)  // G5
  }, [createTone])

  const playSlotSpinSound = useCallback(() => {
    // Sonido de carretes girando
    for (let i = 0; i < 10; i++) {
      setTimeout(() => {
        createTone(200 + Math.random() * 400, 0.05, 'sawtooth')
      }, i * 50)
    }
  }, [createTone])

  const playSlotWinSound = useCallback(() => {
    // Sonido de victoria con campanas
    setTimeout(() => createTone(1047, 0.3), 0)   // C6
    setTimeout(() => createTone(1319, 0.3), 150) // E6
    setTimeout(() => createTone(1568, 0.4), 300) // G6
  }, [createTone])

  const playCardFlipSound = useCallback(() => {
    createTone(300, 0.1, 'triangle')
  }, [createTone])

  const playBlackjackWinSound = useCallback(() => {
    // Acorde de victoria
    setTimeout(() => createTone(440, 0.5), 0)    // A4
    setTimeout(() => createTone(554, 0.5), 0)    // C#5
    setTimeout(() => createTone(659, 0.5), 0)    // E5
  }, [createTone])

  const playRaffleSpinSound = useCallback(() => {
    // Sonido de tÃ³mbola girando
    for (let i = 0; i < 20; i++) {
      setTimeout(() => {
        createTone(150 + Math.sin(i * 0.5) * 100, 0.1, 'triangle')
      }, i * 100)
    }
  }, [createTone])

  const playClickSound = useCallback(() => {
    createTone(1000, 0.05, 'square')
  }, [createTone])

  const playErrorSound = useCallback(() => {
    createTone(200, 0.3, 'sawtooth')
  }, [createTone])

  const playJackpotSound = useCallback(() => {
    // Sonido Ã©pico de jackpot
    const notes = [523, 659, 784, 1047, 1319] // C5, E5, G5, C6, E6
    notes.forEach((note, index) => {
      setTimeout(() => {
        createTone(note, 0.4)
        createTone(note * 1.5, 0.4) // ArmonÃ­a
      }, index * 200)
    })
  }, [createTone])

  return {
    playRouletteTickSound,
    playRouletteWinSound,
    playSlotSpinSound,
    playSlotWinSound,
    playCardFlipSound,
    playBlackjackWinSound,
    playRaffleSpinSound,
    playClickSound,
    playErrorSound,
    playJackpotSound,
  }
}
