"use client"

import { useState, useEffect } from 'react'
import { BLACKJACK_CARDS, GAME_CONFIG } from '@/lib/constants'

interface GameState {
  fichasAdministrativas: number
  setFichasAdministrativas: (value: number) => void
  creditosVIP: number
  setCreditosVIP: (value: number) => void
  puntosExito: number
  setPuntosExito: (value: number) => void
  nivelActual: number
  setNivelActual: (value: number) => void
  modoLigero: boolean
  setModoLigero: (value: boolean) => void
}

interface DecisionBlackjackProps {
  gameState: GameState
}

interface Card {
  name: string
  points: number
}

export default function DecisionBlackjack({ gameState }: DecisionBlackjackProps) {
  const [currentBet, setCurrentBet] = useState(GAME_CONFIG.minBet)
  const [playerCards, setPlayerCards] = useState<Card[]>([])
  const [playerScore, setPlayerScore] = useState(0)
  const [gameStatus, setGameStatus] = useState<'waiting' | 'playing' | 'won' | 'lost' | 'bust'>('waiting')
  const [lossCount, setLossCount] = useState(0)
  const [error, setError] = useState<string | null>(null)
  const [showManoSegura, setShowManoSegura] = useState(false)

  const { fichasAdministrativas, setFichasAdministrativas, setPuntosExito, modoLigero } = gameState

  // FunciÃ³n para obtener una carta aleatoria
  const getRandomCard = (): Card => {
    const cardType = BLACKJACK_CARDS[Math.floor(Math.random() * BLACKJACK_CARDS.length)]
    const points = cardType.points[Math.floor(Math.random() * cardType.points.length)]
    return {
      name: cardType.name,
      points
    }
  }

  // Iniciar nueva partida
  const startNewGame = () => {
    if (fichasAdministrativas < currentBet) {
      setError('Fichas insuficientes para apostar')
      return
    }

    setError(null)
    setFichasAdministrativas(fichasAdministrativas - currentBet)
    
    // Dar dos cartas iniciales
    const initialCards = [getRandomCard(), getRandomCard()]
    setPlayerCards(initialCards)
    setPlayerScore(initialCards.reduce((sum, card) => sum + card.points, 0))
    setGameStatus('playing')
  }

  // Pedir otra carta
  const hitCard = () => {
    if (gameStatus !== 'playing') return

    const newCard = getRandomCard()
    const newCards = [...playerCards, newCard]
    const newScore = newCards.reduce((sum, card) => sum + card.points, 0)

    setPlayerCards(newCards)
    setPlayerScore(newScore)

    if (newScore > 21) {
      setGameStatus('bust')
      setLossCount(prev => prev + 1)
    } else if (newScore === 21) {
      setGameStatus('won')
      const winAmount = currentBet * 2
      setFichasAdministrativas(fichasAdministrativas + winAmount)
      setPuntosExito(gameState.puntosExito + Math.floor(winAmount / 5))
      setLossCount(0)
    }
  }

  // Plantarse
  const stand = () => {
    if (gameStatus !== 'playing') return

    if (playerScore >= 17 && playerScore <= 21) {
      setGameStatus('won')
      const winAmount = Math.floor(currentBet * (1 + (21 - playerScore) * 0.1))
      setFichasAdministrativas(fichasAdministrativas + winAmount)
      setPuntosExito(gameState.puntosExito + Math.floor(winAmount / 5))
      setLossCount(0)
    } else {
      setGameStatus('lost')
      setLossCount(prev => prev + 1)
    }
  }

  // Mano Segura (disponible despuÃ©s de 3 derrotas)
  const manoSegura = () => {
    if (fichasAdministrativas < currentBet) {
      setError('Fichas insuficientes para la Mano Segura')
      return
    }

    setError(null)
    setFichasAdministrativas(fichasAdministrativas - currentBet)
    
    // Generar mano segura (18-20 puntos)
    const safeScore = 18 + Math.floor(Math.random() * 3)
    const safeCards: Card[] = []
    let currentScore = 0
    
    while (currentScore < safeScore) {
      const remainingPoints = safeScore - currentScore
      const availableCards = BLACKJACK_CARDS.flatMap(cardType => 
        cardType.points.filter(points => points <= remainingPoints).map(points => ({
          name: cardType.name,
          points
        }))
      )
      
      if (availableCards.length > 0) {
        const card = availableCards[Math.floor(Math.random() * availableCards.length)]
        safeCards.push(card)
        currentScore += card.points
      } else {
        break
      }
    }

    setPlayerCards(safeCards)
    setPlayerScore(currentScore)
    setGameStatus('won')
    
    const winAmount = Math.floor(currentBet * 1.5)
    setFichasAdministrativas(fichasAdministrativas + winAmount)
    setPuntosExito(gameState.puntosExito + Math.floor(winAmount / 5))
    setLossCount(0)
  }

  // Efecto para mostrar Mano Segura
  useEffect(() => {
    setShowManoSegura(lossCount >= 3)
  }, [lossCount])

  // Reset game
  const resetGame = () => {
    setPlayerCards([])
    setPlayerScore(0)
    setGameStatus('waiting')
    setError(null)
  }

  return (
    <div className="p-6 space-y-6">
      <div className="text-center">
        <h2 className="text-3xl font-casino mb-2 bg-gradient-to-r from-neonYellow to-neonGreen bg-clip-text text-transparent">
          Blackjack de Decisiones
        </h2>
        <p className="text-gray-400">
          Toma decisiones estratÃ©gicas para llegar a 21 Puntos de Ã‰xito
        </p>
      </div>

      {/* Mesa de Juego */}
      <div className="max-w-2xl mx-auto">
        <div className="bg-gradient-to-br from-green-900 to-green-800 rounded-xl p-6 border-2 border-neonYellow/30 shadow-lg shadow-neonYellow/20">
          
          {/* InformaciÃ³n del Juego */}
          <div className="text-center mb-6">
            <div className="flex justify-center items-center space-x-8 mb-4">
              <div className="text-center">
                <div className="text-2xl font-casino text-neonYellow">
                  {playerScore}
                </div>
                <div className="text-sm text-gray-300">Puntos de Ã‰xito</div>
              </div>
              
              <div className="text-center">
                <div className="text-lg font-casino text-neonTurquoise">
                  {currentBet}
                </div>
                <div className="text-sm text-gray-300">Apuesta</div>
              </div>
              
              {lossCount > 0 && (
                <div className="text-center">
                  <div className="text-lg font-casino text-red-400">
                    {lossCount}
                  </div>
                  <div className="text-sm text-gray-300">Derrotas</div>
                </div>
              )}
            </div>

            {/* Estado del Juego */}
            {gameStatus === 'won' && (
              <div className="p-3 bg-green-900/50 border border-neonGreen rounded-lg">
                <p className="text-neonGreen font-casino">Â¡DECISIÃ“N EXITOSA!</p>
              </div>
            )}
            {gameStatus === 'lost' && (
              <div className="p-3 bg-red-900/50 border border-red-500 rounded-lg">
                <p className="text-red-400 font-casino">DecisiÃ³n Fallida</p>
              </div>
            )}
            {gameStatus === 'bust' && (
              <div className="p-3 bg-red-900/50 border border-red-500 rounded-lg">
                <p className="text-red-400 font-casino">Â¡SOBRECARGA! MÃ¡s de 21 puntos</p>
              </div>
            )}
          </div>

          {/* Cartas del Jugador */}
          <div className="mb-6">
            <h3 className="text-center text-lg font-casino text-neonTurquoise mb-4">
              Tus Decisiones
            </h3>
            <div className="flex flex-wrap justify-center gap-3">
              {playerCards.map((card, index) => (
                <div
                  key={index}
                  className={`bg-white text-black rounded-lg p-4 min-w-[100px] text-center border-2 transition-all duration-300 ${
                    !modoLigero ? 'shadow-lg transform hover:scale-105' : ''
                  }`}
                  style={{ 
                    borderColor: index === playerCards.length - 1 ? '#FFFF00' : '#ccc',
                    animationDelay: `${index * 0.1}s`
                  }}
                >
                  <div className="font-bold text-lg">{card.name}</div>
                  <div className="text-2xl font-casino text-blue-600">{card.points}</div>
                </div>
              ))}
              
              {playerCards.length === 0 && (
                <div className="text-gray-400 text-center py-8">
                  Presiona "Nueva Partida" para comenzar
                </div>
              )}
            </div>
          </div>

          {/* Controles de Apuesta */}
          {gameStatus === 'waiting' && (
            <div className="text-center mb-4">
              <label className="block text-sm text-gray-300 mb-2">
                Apuesta (Fichas Administrativas)
              </label>
              <div className="flex items-center justify-center space-x-4 mb-4">
                <button
                  onClick={() => setCurrentBet(Math.max(GAME_CONFIG.minBet, currentBet - 10))}
                  className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded"
                >
                  -10
                </button>
                <span className="font-casino text-xl text-neonYellow min-w-[80px]">
                  {currentBet}
                </span>
                <button
                  onClick={() => setCurrentBet(Math.min(GAME_CONFIG.maxBet, currentBet + 10))}
                  className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded"
                >
                  +10
                </button>
              </div>
            </div>
          )}

          {/* Botones de AcciÃ³n */}
          <div className="flex flex-wrap justify-center gap-3">
            {gameStatus === 'waiting' && (
              <button
                onClick={startNewGame}
                disabled={fichasAdministrativas < currentBet}
                className={`px-6 py-3 rounded-lg font-casino transition-all duration-300 ${
                  fichasAdministrativas < currentBet
                    ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
                    : 'bg-gradient-to-r from-neonYellow to-neonGreen text-black hover:shadow-lg hover:shadow-neonYellow/30'
                }`}
              >
                Nueva Partida
              </button>
            )}

            {gameStatus === 'playing' && (
              <>
                <button
                  onClick={hitCard}
                  className="px-6 py-3 bg-neonTurquoise text-black rounded-lg font-casino hover:bg-neonTurquoise/80 transition-all duration-300"
                >
                  Pedir Carta
                </button>
                <button
                  onClick={stand}
                  className="px-6 py-3 bg-neonFuchsia text-white rounded-lg font-casino hover:bg-neonFuchsia/80 transition-all duration-300"
                >
                  Plantarse
                </button>
              </>
            )}

            {(gameStatus === 'won' || gameStatus === 'lost' || gameStatus === 'bust') && (
              <button
                onClick={resetGame}
                className="px-6 py-3 bg-gray-600 text-white rounded-lg font-casino hover:bg-gray-500 transition-all duration-300"
              >
                Jugar de Nuevo
              </button>
            )}

            {showManoSegura && gameStatus === 'waiting' && (
              <button
                onClick={manoSegura}
                disabled={fichasAdministrativas < currentBet}
                className={`px-6 py-3 rounded-lg font-casino transition-all duration-300 ${
                  fichasAdministrativas < currentBet
                    ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
                    : 'bg-gradient-to-r from-neonOrange to-red-500 text-white hover:shadow-lg hover:shadow-neonOrange/30'
                }`}
              >
                Mano Segura
              </button>
            )}
          </div>
        </div>
      </div>

      {/* Error */}
      {error && (
        <div className="text-center p-3 bg-red-900/30 border border-red-500/50 rounded-lg">
          <p className="text-red-400">{error}</p>
        </div>
      )}

      {/* Reglas del Juego */}
      <div className="bg-gray-800/50 rounded-lg p-4">
        <h3 className="font-casino text-lg text-neonYellow mb-3 text-center">
          Reglas del Blackjack de Decisiones
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
          <div className="space-y-2">
            <div className="flex justify-between">
              <span>Objetivo:</span>
              <span className="text-neonGreen">Llegar a 21 puntos exactos</span>
            </div>
            <div className="flex justify-between">
              <span>Plantarse:</span>
              <span className="text-neonTurquoise">Entre 17-21 puntos</span>
            </div>
            <div className="flex justify-between">
              <span>Sobrecarga:</span>
              <span className="text-red-400">MÃ¡s de 21 puntos</span>
            </div>
          </div>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span>Mano Segura:</span>
              <span className="text-neonOrange">Tras 3 derrotas</span>
            </div>
            <div className="flex justify-between">
              <span>Premio 21:</span>
              <span className="text-neonYellow">Apuesta Ã— 2</span>
            </div>
            <div className="flex justify-between">
              <span>Premio Normal:</span>
              <span className="text-neonYellow">Apuesta Ã— 1.1-1.5</span>
            </div>
          </div>
        </div>
        
        <div className="mt-4 pt-4 border-t border-gray-600">
          <h4 className="font-casino text-neonTurquoise mb-2">Cartas de DecisiÃ³n:</h4>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
            {BLACKJACK_CARDS.map((cardType, index) => (
              <div key={index} className="bg-gray-700/50 p-2 rounded text-center">
                <div className="font-bold text-sm">{cardType.name}</div>
                <div className="text-xs text-gray-400">
                  {cardType.points.join(', ')} pts
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
