"use client"

import { useEffect, useRef, useState } from 'react'
import { ROULETTE_SECTORS, GAME_CONFIG } from '@/lib/constants'
import { useGameSounds } from '@/hooks/useGameSounds'

interface GameState {
  fichasAdministrativas: number
  setFichasAdministrativas: (value: number) => void
  creditosVIP: number
  setCreditosVIP: (value: number) => void
  puntosExito: number
  setPuntosExito: (value: number) => void
  nivelActual: number
  setNivelActual: (value: number) => void
  modoLigero: boolean
  setModoLigero: (value: boolean) => void
}

interface ProjectRouletteProps {
  gameState: GameState
}

export default function ProjectRoulette({ gameState }: ProjectRouletteProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isSpinning, setIsSpinning] = useState(false)
  const [currentBet, setCurrentBet] = useState(GAME_CONFIG.minBet)
  const [lastResult, setLastResult] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [rotation, setRotation] = useState(0)

  const { fichasAdministrativas, setFichasAdministrativas, setPuntosExito, modoLigero } = gameState
  const { playRouletteTickSound, playRouletteWinSound, playErrorSound, playClickSound } = useGameSounds()

  // Dibujar la ruleta en el canvas
  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    const centerX = canvas.width / 2
    const centerY = canvas.height / 2
    const radius = Math.min(centerX, centerY) - 20

    // Limpiar canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height)

    // Guardar estado del contexto
    ctx.save()
    ctx.translate(centerX, centerY)
    ctx.rotate((rotation * Math.PI) / 180)

    // Dibujar sectores
    const sectorAngle = (2 * Math.PI) / ROULETTE_SECTORS.length
    
    ROULETTE_SECTORS.forEach((sector, index) => {
      const startAngle = index * sectorAngle
      const endAngle = (index + 1) * sectorAngle

      // Dibujar sector
      ctx.beginPath()
      ctx.moveTo(0, 0)
      ctx.arc(0, 0, radius, startAngle, endAngle)
      ctx.closePath()
      ctx.fillStyle = sector.color
      ctx.fill()
      ctx.strokeStyle = '#000'
      ctx.lineWidth = 2
      ctx.stroke()

      // Dibujar texto
      ctx.save()
      ctx.rotate(startAngle + sectorAngle / 2)
      ctx.fillStyle = '#000'
      ctx.font = modoLigero ? '12px Arial' : '14px Arial'
      ctx.textAlign = 'center'
      ctx.fillText(sector.name, radius * 0.7, 5)
      ctx.restore()
    })

    // Restaurar estado del contexto
    ctx.restore()

    // Dibujar indicador
    ctx.fillStyle = '#fff'
    ctx.beginPath()
    ctx.moveTo(centerX, 20)
    ctx.lineTo(centerX - 10, 40)
    ctx.lineTo(centerX + 10, 40)
    ctx.closePath()
    ctx.fill()
    ctx.strokeStyle = '#000'
    ctx.stroke()

  }, [rotation, modoLigero])

  const spinRoulette = async () => {
    if (isSpinning || fichasAdministrativas < currentBet) {
      setError('Fichas insuficientes o ruleta en movimiento')
      playErrorSound()
      return
    }

    setError(null)
    setIsSpinning(true)
    setLastResult(null)

    // Deducir apuesta
    setFichasAdministrativas(fichasAdministrativas - currentBet)

    // Simular giro
    const spins = 5 + Math.random() * 5 // 5-10 vueltas
    const finalRotation = spins * 360 + Math.random() * 360
    
    // AnimaciÃ³n de giro
    const duration = modoLigero ? 2000 : 4000
    const startTime = Date.now()
    const startRotation = rotation

    // Sonidos de tick durante el giro
    const tickInterval = setInterval(() => {
      if (!modoLigero) {
        playRouletteTickSound()
      }
    }, 100)

    const animate = () => {
      const elapsed = Date.now() - startTime
      const progress = Math.min(elapsed / duration, 1)
      
      // Easing function para desaceleraciÃ³n
      const easeOut = 1 - Math.pow(1 - progress, 3)
      const currentRotation = startRotation + finalRotation * easeOut
      
      setRotation(currentRotation % 360)

      if (progress < 1) {
        requestAnimationFrame(animate)
      } else {
        clearInterval(tickInterval)
        
        // Determinar sector ganador
        const normalizedRotation = (360 - (currentRotation % 360)) % 360
        const sectorAngle = 360 / ROULETTE_SECTORS.length
        const winningIndex = Math.floor(normalizedRotation / sectorAngle)
        const winningSector = ROULETTE_SECTORS[winningIndex]

        setLastResult(winningSector.name)
        
        // Calcular premio
        const prize = Math.floor(currentBet * (winningSector.reward / 50))
        setFichasAdministrativas(fichasAdministrativas + prize)
        setPuntosExito(gameState.puntosExito + Math.floor(prize / 10))
        
        // Sonido de victoria
        setTimeout(() => {
          playRouletteWinSound()
        }, 500)
        
        setIsSpinning(false)
      }
    }

    animate()
  }

  return (
    <div className="p-6 space-y-6">
      <div className="text-center">
        <h2 className="text-3xl font-casino mb-2 bg-gradient-to-r from-neonFuchsia to-neonTurquoise bg-clip-text text-transparent">
          Ruleta de Proyectos
        </h2>
        <p className="text-gray-400">
          Gira la ruleta y descubre tu prÃ³ximo proyecto corporativo
        </p>
      </div>

      {/* Canvas de la Ruleta */}
      <div className="flex justify-center">
        <div className="relative">
          <canvas
            ref={canvasRef}
            width={400}
            height={400}
            className="border-2 border-neonFuchsia/30 rounded-full shadow-lg shadow-neonFuchsia/20"
          />
          {isSpinning && !modoLigero && (
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="text-white font-casino text-lg animate-pulse">
                Girando...
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Controles de Apuesta */}
      <div className="max-w-md mx-auto space-y-4">
        <div className="text-center">
          <label className="block text-sm text-gray-400 mb-2">
            Apuesta (Fichas Administrativas)
          </label>
          <div className="flex items-center justify-center space-x-4">
            <button
              onClick={() => {
                playClickSound()
                setCurrentBet(Math.max(GAME_CONFIG.minBet, currentBet - 10))
              }}
              disabled={isSpinning}
              className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded disabled:opacity-50 transition-all duration-200 hover:scale-105"
            >
              -10
            </button>
            <span className="font-casino text-xl text-neonYellow min-w-[80px] animate-glow">
              {currentBet}
            </span>
            <button
              onClick={() => {
                playClickSound()
                setCurrentBet(Math.min(GAME_CONFIG.maxBet, currentBet + 10))
              }}
              disabled={isSpinning}
              className="px-3 py-1 bg-gray-700 hover:bg-gray-600 rounded disabled:opacity-50 transition-all duration-200 hover:scale-105"
            >
              +10
            </button>
          </div>
        </div>

        <button
          onClick={spinRoulette}
          disabled={isSpinning || fichasAdministrativas < currentBet}
          className={`w-full py-3 px-6 rounded-lg font-casino text-lg transition-all duration-300 ${
            isSpinning || fichasAdministrativas < currentBet
              ? 'bg-gray-600 text-gray-400 cursor-not-allowed'
              : 'bg-gradient-to-r from-neonFuchsia to-neonTurquoise hover:shadow-lg hover:shadow-neonFuchsia/30 text-white'
          }`}
        >
          {isSpinning ? 'Girando...' : 'GIRAR RULETA'}
        </button>
      </div>

      {/* Resultados y Errores */}
      {error && (
        <div className="text-center p-3 bg-red-900/30 border border-red-500/50 rounded-lg">
          <p className="text-red-400">{error}</p>
        </div>
      )}

      {lastResult && (
        <div className="text-center p-4 bg-green-900/30 border border-neonGreen/50 rounded-lg">
          <p className="text-neonGreen font-casino text-lg">
            Â¡Proyecto asignado: {lastResult}!
          </p>
        </div>
      )}

      {/* Tabla de Sectores */}
      <div className="bg-gray-800/50 rounded-lg p-4">
        <h3 className="font-casino text-lg text-neonTurquoise mb-3 text-center">
          Sectores de la Ruleta
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-2 text-sm">
          {ROULETTE_SECTORS.map((sector) => (
            <div key={sector.id} className="flex justify-between items-center p-2 rounded">
              <div className="flex items-center space-x-2">
                <div 
                  className="w-4 h-4 rounded-full border border-gray-400"
                  style={{ backgroundColor: sector.color }}
                />
                <span>{sector.name}</span>
              </div>
              <span className="text-neonYellow font-casino">
                {sector.reward}x
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}
